.work__filters {
  display: flex;
  align-items: center;
  column-gap: 1.875rem;
  margin-bottom: 2.5rem;
  flex-wrap: wrap; /* allow wrapping on small screens */
  gap: 1rem;
}

.work__item {
  cursor: pointer;
  font-weight: var(--font-bold);
  transition: 0.3s;
  white-space: nowrap;
}

.work__item:hover {
  color: var(--first-color);
}

.work__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.875rem;
}

.work__card {
  position: relative;
  overflow: hidden;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
}

.work__mask {
  background: #6c6ce5;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  transition: 0.3s;
  opacity: 0;
}

.work__card:hover .work__mask {
  background: #6c6ce5;
  opacity: 0.9;
}

.work__category {
  color: #fff;
  background-color: var(--first-color);
  border-bottom-left-radius: 1.4rem;
  border-bottom-right-radius: 0.98rem;
  position: absolute;
  top: 0;
  left: 1.5rem;
  font-size: var(--small-font-size);
  display: inline-block;
  padding: 0 1.9rem 0.625rem;
  transform: translateY(-40px);
  transition: 0.3s;
}

.work__title {
  color: #fff;
  font-size: var(--h3-font-size);
  margin: 0 0 0.98rem;
  padding: 0 1.25rem;
  position: absolute;
  top: 3.75rem;
  transform: translateY(30px);
  transition: 0.3s;
  opacity: 0;
}

.work__button {
  color: #fff;
  position: absolute;
  left: 1.5rem;
  bottom: 1.5rem;
  font-size: var(--h3-font-size);
  display: block;
  background-color: #ffd15c;
  height: 40px;
  width: 40px;
  cursor: pointer;
  border-radius: 50%;
  text-align: center;
  line-height: 42px;
  transition: 0.3s;
  opacity: 0;
}

.work__card:hover .work__button {
  opacity: 1;
}

.work__card:hover .work__title,
.work__card:hover .work__category {
  opacity: 1;
  transform: translateY(0);
}

/* ====== RESPONSIVE STYLES ====== */

/* Tablets (≤992px) */
@media (max-width: 992px) {
  .work__container {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }

  .work__filters {
    justify-content: center;
  }
}

/* Mobiles (≤576px) */
@media (max-width: 576px) {
  .work__container {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .work__filters {
    justify-content: center;
    column-gap: 1rem;
  }

  .work__item {
    font-size: 0.9rem;
  }

  .work__title {
    font-size: 1.25rem;
  }
}
